body {
  font-size:0.9em;
  font-family: 'Noto Sans TC', sans-serif;
  background-color: #eaeaea;
  @include media-breakpoint-up(md) {
    font-size: 0.95em;
  }
  @include media-breakpoint-up(lg) {
    font-size: 1em;
  }
}
input,button { 
  &:focus{
    outline:none;
  }   
}
.btn {
  font-size: 1em;
}
@include media-breakpoint-up(lg) {
  .w-lg-25 {
    width: 25%;
  }
}
.bg-cover {
    background-size: cover;
    background-position: center center;
}
.rounded-big {
  border-radius: 100px;
}
.badge {
  margin-right: 0.5em;
  padding: .5em 1em;
}
.badge-news {
  color: #fff;
  background-color: $secondary;
}
.badge-system {
  color: #fff;
  background-color: #00c783
}
.logo img {
  height: 25px;
  margin-bottom: 0.25em;
  margin-top: 0.25em;
  @include media-breakpoint-up(md) {
    height: 35px;
  }
  @include media-breakpoint-up(xl) {
    height: 40px;
  }
}
.max-width-lg {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
}
.fancy-menu {
  // display: inline-block;
  border:none;
}
.fancy-menu span {
  background: $primary;
  border-radius: 2px;
  display: block;
  height: 2px;
  margin-top: 4px;
  padding: 0;
  position: relative;
  transition: all 0.2s ease;
  width: 30px;
}
.fancy-menu.active span:nth-of-type(1) {
  transform: rotate3d(0, 0, 1, 45deg);
  top: 6px;
}
.fancy-menu.active span:nth-of-type(2) {
  opacity: 0;
}
.fancy-menu.active span:nth-of-type(3) {
  transform: rotate3d(0,0,1,-45deg);
  top: -6px;
}
.navbar {
  padding: 0;
  background-color: $white;
  border-bottom: 1px solid $light;
}
@include media-breakpoint-down(md) {
  .navbar-collapse {
    position: absolute;
    top:100%;
    width: 100%;
  }
  .navbar-nav {
    height: 100vh;
    overflow-y: auto;
    // padding: 2em;
  }
  .nav-link {
    font-size: 1.2em;
  }
}
.navbar-nav {
  background-color:rgba($white, .95);
  .nav-link  {
    color:$body-color;
    padding-left: 2em !important;
    padding-right: 2em !important;
    padding-top: 1em;
    padding-bottom: 1em;
    transition: all .3s;
    &:hover {
      color: $primary;
    }
  }
  @include media-breakpoint-up(lg) {
    .nav-item {
      border-left: 1px solid rgba($light, 1);
    }
    .nav-link  {
      padding-top: 1.5em;
      padding-bottom: 1.5em;
      margin-left: 2px;
      margin-right: 2px;
      position: relative;
      &::before {
        transition: all .3s;
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";
        width: 100%;
        height: 2px;
        background-color: $primary;
      }
      &:hover {
        &::before {
          height: 4px;
        }
      }
    }
  }
}
.dropdown {
  position: inherit;
}
.dropdown-icon {
  display: flex;
  &::after{
    content: '\f067';
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    display: inline-block;
    margin-left:auto;
  }
  @include media-breakpoint-up(lg) {
    &::after{
      content: "\f078";
      margin-left: 5px;
    }
  }
}
.dropdown-menu {
  background-color: transparent;
  border-right:none;
  border-left:none;
  border-bottom:none;
  margin-left: 1.5em;
  margin-right: 1.5em;
  @include media-breakpoint-up(lg) { 
    background-color:rgba($white, .85);
    padding: 0 1em;
    margin: 0;
    width: 100%;
    text-align: right;
  }
}
.dropdown-item {
  padding-top: 1em;
  padding-bottom: 1em;
  color: $secondary;
  transition: all .3s;
  width: auto;
  &:hover {
    color: $primary;
    background-color: transparent;
  }
  @include media-breakpoint-up(lg) { 
    display: inline-block;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
  }
}
.btn-login {
  align-self: stretch;
  display: flex;
  a {
    font-size: 1rem;
    display: flex;
    align-items: center;
    &.login {
      padding-left: 1em;
      padding-right: 1em;
    }
  }
  span {
    display: none;
  }
  @include media-breakpoint-up(md) {
    a.login {
      padding-left: 1.25em;
      padding-right: 1.25em;
    }
  }
  @include media-breakpoint-up(lg) {
    a.login {
      padding-left: 2em;
      padding-right: 2em;
    }
  }
  @include media-breakpoint-up(xl) {
    a.login {
      padding-left: 3em;
      padding-right: 3em;
    }
    span {
      display:inline-block;
      margin-right: .2em;
    }
  }
}
.img-user {
  // background-color: white;
  background: white url("../images/img-user-default.png") center no-repeat;
  background-size: cover;
  width: 2em;
  height: 2em;
  border-radius: 100px;
  overflow: hidden;
  img {
    max-width: 100%;
    height: auto;
  }
  @include media-breakpoint-up(lg) {
    width: 3em;
    height: 3em;
  }

