    background-color: $gray-200;
    width: 8em;
    height: 8em;
    margin-left: auto;
    margin-right: auto;
    @include media-breakpoint-up(lg) {
      width: 200px;
      height: 200px;
    }
  }
}
.dropdown-menu-user {
  border: none;
  border-radius: 5px;
  background-color: white;
  width: auto;
  padding: .5em 1em;
  top:105%;
  right: 3px;
  left: auto;
  margin-left: 0;
  margin-right: 0;
  box-shadow: 0 0 5px rgba($secondary,0.5);
  // &::before {
    //   content: "";
    //   position: absolute;
    //   color: white;
    //   top: -0.5em;
    //   right: .5em;
    //   border-top: 0 ;
    //   border-right: 0.5em solid transparent;
    //   border-bottom: 0.5em solid;
    //   border-left: 0.5em solid transparent;
    // }
    a {
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    text-decoration: none;
    color: $secondary;
    transition: all .3s;
    // font-size: .9em;
    &:hover {
      color: lighten($secondary, 30)
    }
  }
}
.login{
  padding: 1em;
  >div {
    background-color:white;
    padding-top: 1em;
    padding-bottom: 1em;
  }
}
.login-other {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  text-align: center;
  @include media-breakpoint-up(sm) {
    border-left: 1px solid $light;
  }
  @include media-breakpoint-up(lg) {
    border-left: none;
    border-top: 1px solid $light;
  }
}
.index-member {
  margin-top: 1em;
  padding: 1em;
  background-color: #fff;
}

.member {
  display: flex;
  flex-direction: column;
  justify-content:space-between;
  height: 100vh;
}
.member-form {
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  >* {
    background-color: #fff;
    width: 100%;
    max-width: 500px;
    @include media-breakpoint-up(sm) {
      width: 60vw;
    }
  }
}
.member-data {
  min-height: 80vh;
  background-color: white;
  padding: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
}
.icon-group {
  display: flex;
  justify-content: center;
  a {
    width: 1.75em;
    height: 1.75em;
    font-size: 1.5em;
    margin-right: 0.25em;
    padding: 0.25em;
    display: flex;
    align-items: center;
    justify-content: center;
    &:last-child {
      margin-right: 0;
    }
  }
  @include media-breakpoint-up(xl) {
    a {
      font-size: 2em;
    }
  }
  @include media-breakpoint-down(sm) {
    &-sm {
      a {
        width: 30px;
        height: 30px;
        font-size: 1.3em;
      }
    }
  }
}
/*i*/.icon {
  position: relative;
  width: 150%;
  height: 1.5em;
  &::after {
    position: absolute;
    content: '';
    top: 0;
    bottom: 0;
    right: 0%;
    left: 0%;
    background-position: center;
    background-repeat: no-repeat;
    // background-color: #fff;
  }
  &-apk {
    &::after{
      background-size: 100% auto;
      background-image:url(../images/svg/apk.svg);
    }
  }
  &-play {
    &::after {
      background-size:90% auto;
      background-image:url(../images/svg/play.svg);
    }
  }
}
.btn-black {
  color: white;
  background-color: black;
  &:hover {
    color: white;
    background-color:$gray-800;
  }
}
.btn-android {
  color: white;
  background-color: $android;
  &:hover {
    color: white;
    background-color: darken($android, 10);
  }
}
.btn-fb {
  color: white;
  background-color: $fb;
  &:hover {
    color: white;
    background-color: darken($fb, 10);
  }
}
.btn-apk {
  color: white;
  background-color: $yellow;
  &:hover {
    color: white;
    background-color: darken($yellow, 5);
  }
}
.content-home {
  background-color: #fff;
  margin-top: 3em;
  @include media-breakpoint-up (sm) {
    margin-top: 4em;
  }
}
.tit-home {
  display: flex;
  align-items: flex-end;
  margin-right: 0.5em;
  h3 {
    background-color: $primary;
    color: white;
    font-weight:medium;
    font-size: 1.25em;
    padding-top: 0.5em;
    padding-left: 0.5em;
    margin-top: -1.25em;
    margin-bottom: 0;
    max-width: 250px;
    &::after {
      display:block;
      float: right;
      content: '';
      width: 50px;
      height: 40px;
      margin-left: 1em;
      background: url(../images/tit-bg.png) center no-repeat;
      background-size: cover;
    }
  }
  @include media-breakpoint-up(md) {
    margin: 0 1.5em;
    h3 {
      font-size: 1.5em;
      margin-top: -2em;
      &::after {
        margin-top: 1.25em;
        width: 70px;
        height: 60px;
      }
    }
  }
}
.btn-more {
  position: relative;
  transition: all .3s;
  font-weight: bold;
  font-size: 0.9em;
  &::after {
    content: '\f054';
    font-weight: 900;
    font-family: 'Font Awesome 5 Free';
    margin-left: 0.25em;
  }
  &:hover {
    text-decoration: none;
    transform: translateX(.5em);
  }
  @include media-breakpoint-up (md) {
    font-size: 1em;
  }
}

.card {

}
.card-gift {
  padding: .25em;
  max-width: 100px;
  text-align: center;
  transition: all .3s ease;
  &:hover {
    background-color: $light;
    box-shadow: 0px 0px 3px rgba($secondary,0.3);
  }
  p {
    margin-bottom: 0.5em;
    font-size: .9em;
  }
  img {
    margin-bottom: 0.5em;
  }
  @include media-breakpoint-up(sm) {
    padding: .5em;
    max-width: 130px;
    margin: .25em;
    p {
      font-size: 0.95em;
    }
  }
  @include media-breakpoint-up(md) {
      max-width: 150px;
    p  {
      font-size: 1em;
    }
  }
  // @include media-breakpoint-up(lg) {
  // }
}
.card-play {
  padding: .5em;
  display: flex;
  max-width: 275px;
  &-text {
    display: flex;
    flex-direction: column;
    margin-left: 1em;
    width: 50%;
  }
  &-img {
    width: 45%;
  }
  p {
    margin-bottom: 0;
  }
  @include media-breakpoint-up(md) {
    padding: 1em;
  }
  @include media-breakpoint-up(lg) {
    padding: 1.5em;
    max-width: 350px;
  }
}
.card-game {
  margin-top: 3em;
  padding: 0.25em;
  transition: box-shadow .3s ease;
  &:hover {
    box-shadow: 0px 0px 3px rgba($secondary,0.3);
  }
  p {
    font-size: 1.1em;
    border-bottom: 1px solid $light;
    padding-bottom: 0.25em;
  }
}
.shadow {
  transition: box-shadow .3s ease;
  &:hover {
    box-shadow: 0px 0px 3px rgba($secondary,0.3);
  }
}
.img-game {
  position: relative;
  transition: all .3s;
  &::after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right:1em;
    content: '';
    z-index: 1;
    background:url(../images/img-bg.png) right bottom no-repeat;
    background-size: auto 20%;
    transition: all .3s;
  }
  &:hover{
    img {
      filter: brightness(130%);
    }
    &::after {
      right: 0;
    }
  }
}
.icon-category {
  position: absolute;
  top: -1.5em;
  right: 1em;
  background-color:$primary;
  padding: 1.5em .5em .5em;
  color: white;
  font-weight:600;
  z-index: 2;
}
.img-128 {
  width: 100%;
  height: auto;
  max-width: 128px;
  border-radius: 25%;
}
.img-200 {
  width: 100%;
  height: auto;
  max-width: 200px;
  border-radius: 25%;
}
.img-15 {
  width: 15%;
  height: auto;
  max-width: 50px;
  max-height: 50px;
}
.text-date {
  font-size: 0.9em;
  color: $secondary;
  border-right: 1px solid $gray-200;
  padding: 0.5em;
}
footer {
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  background-color:#303131;
  color: $secondary;
  @include media-breakpoint-up(md) {
    padding-top: 3em;
    padding-bottom: 3em;
  }
}
.copyright {
  a {
    display: block;
  }
  img {
    width: 100px;
    height: auto;
  }
  @include media-breakpoint-up (md) {
    img {
      width: 135px;
    }
  }
}
.footer-nav {
  margin-top: 1em;
  margin-bottom: 1em;
  a {
    padding-left: 0.5em;
    padding-right: 0.5em;
    color: $secondary;
    font-size: 0.95em;
    border-right: 1px solid $secondary;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      border-right: none;
    }
    &:hover {
      text-decoration: none;
      color: lighten($secondary, 10);
    }
  }
}
.back-to-top {
  border-radius: 5px;
  position: fixed;
  z-index: 200;
  bottom: 10px;
  right: 10px;
  padding: 0.5em 1em;
  background-color: $primary;
  color:$white;
  transition: opacity .3s;
  opacity: .5;
  &:hover {
    opacity: 1;
  }
}
/*page
  ---
*/
.page-bar {
  align-items: center;
  margin-bottom: .5em;
  @include media-breakpoint-up (md) {
    margin-bottom: 1em;
  }
}
.page-tit {
  margin: .5em -1em;
  position: relative;
  text-align: right;
  &-img {
    position: relative;
    display: inline-block;
    // width: 100%;
    // height: 25vh;
    // max-width: 1000px;
    // max-height: 150px;
    // background: url(../images/banner/tit-banner.jpg) center no-repeat;
    // background-size: cover;
    &::after{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      content: '';
      background-image: linear-gradient(90deg, #fff 15%, rgba(#fff,0) 100%);
      z-index: 1;
    }
  }
  &-text {
    position: absolute;
    top: 0;
    z-index: 2;
    text-align: left;
    padding: 0.5em 1em;
    p {
      line-height: 1em;
      margin-bottom: 0;
      &:first-child {
        margin-top: 1em;
      }
    }
    .h2 {
      text-transform:uppercase;
      font-weight: bold;
    }
    &::before  {
      position: absolute;
      content: '';
      width: 100%;
      height: 2px;
      background-color: $primary;
    }
  }
  img {
    max-width: 100%;
    height: auto;
    
  }
  @include media-breakpoint-up (sm) {
    p {
      font-size: 1.5em;
      &:first-child {
        margin-top: 1.5em;
      }
    }
    .h2 {
      font-size: 3em;
    }
    &-img {
      &::after {
        background-image: linear-gradient(90deg, #fff 0%, rgba(#fff,0) 60%);
      }
    }
    &-text {
      &::before {
        height: 3px;
      }
    }
  }
  @include media-breakpoint-up (lg) {
    margin: 1em 0;
    // &-img {
    //   max-height: 250px;
    // }
    &-text {
      p {
        font-size: 1.8em;
        &:first-child {
          margin-top: 2em;
        }
      }
      .h2 {
        font-size: 4.5em;
      }
    }
  }
}
.nav-page {
  margin-top: .5em;
  margin-bottom: .5em;
  /*a*/.nav-link {
    color: $body-color;
    font-size:1em;
    padding: .5em 1em;
    // &:hover {
    //   color: $secondary;
    // }
  }
}
a.mixitup-control-active.mixitup-control-active {
  background-color: theme-color('primary');
  color: theme-color('light'); 
}
/*form*/
.input-rounded {
  border-radius: 100px;
}
.input-search {
  position: absolute;
  right: 0;
  z-index: 100;
}
/**/
.pagination {
  .page-item {
    &.active {
      .page-link {
        color: $primary;
        background-color: transparent;
      }
    }
    &.disabled {
      .page-link {
        color:rgba($secondary, .75);
        background-color: transparent;
      }
    }
  }
  .page-link {
    font-size: 1.75em;
    font-weight: bold;
    border: none;
    background-color: transparent;
    color: $secondary;
    padding-left: .75em;
    padding-right: .75em;
    &:hover {
      background-color: #fff;
    }
  }
}
/*---GAME---*/
.page-content-game {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: white;
  border: 1px solid $gray-300;
  margin-bottom: 1em;
  >div {
    width: 100%;
    &:last-child {
      padding: 0.5em;
    }
  }
  @include media-breakpoint-up (sm) {
    flex-direction: row;
    >div {
      &:first-child {
        width: 45%;
      }
      &:last-child {
        width: 55%;
      }
    }
  }
  @include media-breakpoint-up (xl) {
    >div {
      &:first-child {
        width: 35%;
      }
      &:last-child {
        width: 65%;
        padding: 1em;
      }
    }
  }
  @media (min-width: 1440px) {
    >div {
      &:first-child {
        width: 30%;
      }
      &:last-child {
        width: 70%;
      }
    }
  }
}
.text-truncate-w {
  display: -webkit-box;
  //-webkit-
  /*!autoprefixer:off*/ 
  -webkit-line-clamp: 2; 
  -webkit-box-orient: vertical;
  /*autoprefixer:on*/
  overflow: hidden;
  text-overflow: ellipsis;
  @include media-breakpoint-up(md) {
    -webkit-line-clamp: 3; 
  }
}
.text-truncate-h {
  display: -webkit-box;
  //-webkit-
  /*!autoprefixer:off*/ 
  -webkit-line-clamp: 10; 
  -webkit-box-orient: vertical;
  /*autoprefixer:on*/
  overflow: hidden;
  text-overflow: ellipsis;
}
.game-intro {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  &-img {
    flex: 1 1 23%;
    margin-right: 2%;
    img {
      border-radius: 25%;
      overflow: hidden;
    }
  }
  &-icon {
    display: flex;
    flex-wrap: wrap;
    flex: 1 1 75%;
    p {
      display: flex;
      width: 100%;
      margin-bottom: .25em;
      padding-top: 0.25em;
      padding-bottom: 0.25em;
    }
    .h4 {
      font-size: 1.2em;
      margin-bottom: 0;
    }
  }
  &-text {
    width: 100%;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    width: 100%;
    align-self: flex-start;
    p {
      &:first-child {
        padding-top: 0.5em;
        padding-bottom: 0.5em;
        margin-bottom: 0.5em;
        border-bottom: 1px solid $gray-200;
      }
      &:last-child {
        margin-bottom: 0;
        color: $secondary;
      }
    }
    
  }
  @include media-breakpoint-up (sm) {
    &-icon {
      p {
        margin-bottom: 0.5em;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
      }
      .h4 {
        font-size: 1.5em;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    &-img {
      flex: none;
    }
  }
}
.game-img {
  margin-top: 1em;
  margin-bottom: 1em;
  img {
    max-height: 200px ;
    width: auto !important;
  }
  @include media-breakpoint-up (md) {
    img {
      max-height: 400px;
    }
  }
  &.type-video {
    a{
      display: block;
      position: relative;
      &::before {
        content: '\f144';
        font-family: 'Font Awesome 5 Free';
        font-weight: 900;
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        font-size: 3em;
        color: $light;
        width: 2em;
        height: 2em;
        text-align: center;
        line-height: 2em;
        opacity: 0.5;
        transition: all .3s;
      }
      &:hover {
        &::before {
          opacity: 1;

        }
      }
    }
  }
}
.btn-gift {
  padding: .25em .5em;
  border-radius: 3px;
  margin-left: auto;
  align-self: center;
  
  @include media-breakpoint-up (xl) {
    &::after {
      content: '';
      margin-left: 0.25em;
    }
  }
}
/*---NEWS---*/
/*ul*/.list-news {
  list-style: none;
  padding-left: 0;
  >li {
    display: flex;
    align-items: baseline;
    line-height: 4em;
    color: $secondary;
    border-bottom: 1px solid $light;
    >a {
      color: $secondary;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}

.news {
  min-height: 80vh;
  background-color: white;
  padding: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
  &-tit {
    background: #eaeaea;
    padding: 1em;
    border-bottom: 3px solid $primary; 
    h2 {
      font-size: 1.25em;
    }
    p {
      color: $secondary;
      margin-bottom: 0;
      // text-align: right;
      // margin-left: 1em;
    }
  }
  &-article {
    color: $secondary;
    line-height: 2rem;
  }
  &-nav {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2em;
    margin-top: 2em;
    a {
      color: $secondary;
      font-size: 1.5em;
      transition: all .3s;
      &:nth-child(2) {
        border: 3px solid $secondary;
        padding: 0em 1.5em;
        &:hover { 
          border: 3px solid $primary;
        }
      }
      &:hover {
        text-decoration: none;
        color:$primary;
        // color: lighten($secondary, 20);
      }
    }
  }
  @include media-breakpoint-up (sm) {
    &-tit {
      h2 {
        font-size: 1.5em;
      }
    }
  }
  @include media-breakpoint-up (lg) {
    &-tit {
      h2 {
        font-size: 2em;
      }
    }
  }
}
/*---GIFT---*/
.gift {
  display: flex;
  align-items: center;
  padding: .5em;
  margin-bottom: 1em;
  background-color: #fff;
  * {
    flex: 1 1 auto;
  }
  &-img {
    max-width: 135px;
    margin-left: auto;
    margin-right: auto;
  }
  &-text {
    padding-left: .5em;
    h6 {
      border-bottom: 1px solid $light;
      margin-bottom: 0.25em;
      padding-bottom: .25em;
      padding-top: 0.25em;
    }
    p {
      margin-bottom: 0;
    }
  }
  a.btn {
    display: block;
    margin-top: .5em;
    padding: .25em;
  }
  @include media-breakpoint-up (sm) {
    // flex-wrap:nowrap;   
    &-img {
      max-width: 130px;
    }
    a.btn {
      display: inline-block;
      padding-left: 1em;
      padding-right: 1em;
    }
  }
  @include media-breakpoint-up (lg) {
    padding: 1em;
    &-text {
      margin-left: 1em;
    }
  }
}
.table {
  margin-bottom: 0;
  thead {
    th {
      // border-bottom-width: 1px;
    // font-weight: inherit;
      // background-color:lighten($primary ,20);
      // background-color:rgba($dark, .5);
      // color: #fff;
    }
  }
  tbody {
    tr {
      color:$secondary;
    }
  }
}
