//form
.custom-radio {
  padding-left: 0;
  margin-right: .5em;
  margin-bottom: 0.5em;
}
.custom-control-input[type="radio"] {
  &+label {
    padding-left: 2.5em;
    padding-right: 1em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    border: 1px solid $gray-300;
    &::before,&::after {
      margin: auto;
      left: 1em;
      top: 0;
      bottom: 0;
    }
  }
  &:checked + label, &:hover + label {
    color: $primary;
    border-color:$primary;
    &::before {
      background-color:rgba($primary,0.5);
    }
  }
}
/*ul*/.note {
  list-style: none;
  padding-left: 1.25rem;
  li {
    // line-height: 1.8rem;
    margin-bottom: 0.5rem;
    text-indent: -1rem;
    &::before {
      content: "";
      margin-right: 0.25rem;
    }
  }
}

.login-other-page {
  height: 100%;
  border-top: 1px solid darken(theme-color('light'), 20);
  padding-top: 2rem;
  @include media-breakpoint-up(sm) {
    border-top: none;
    border-left: 1px solid darken(theme-color('light'), 20);
    padding-top: 0;
  }
}

.fqa-list,.security-list{
  counter-reset:p;
  list-style: none;
  padding: 0;
  >p{
    position: relative;
    padding: .5em;
    margin-left: 2.5em;
    margin-top: 0;
    margin-bottom: 0;
    color: theme-color('light');
    background: theme-color('primary');
    &:before{
      content: counter(p);
      counter-increment: p;
      position: absolute;
      top: 0;
      bottom: 0;
      left: -2.5em;
      background:theme-color('primary');
      padding: .5em;
      width: 2.5em;
      border-right: 2px solid #fff;
      text-align: center;
      }
    }
  .btn-link {
    text-align: left;
    background-color: rgba($primary,0.3);
    color: $secondary;
    text-decoration: none;
    transition: all .3s;
    &:hover {
      background-color:rgba($primary,0.2) ;
    }
  }
}
